<template>
    <v-layout fill-height column >
        <div v-if="endLanding" class="about_landing  animate__animated animate__hinge animate__delay-2s">
            <div class="about_landing__font animate__animated animate__hinge animate__delay-1s">
                About me?
            </div>
        </div>
        <div v-if="!endLanding" class="animate__animated animate__fadeIn router_width d-flex" mode="out-in" style="flex:1; ">
            <div class="d-flex flex-column justify-center " style="width:100%;">
                <div class="contents__frame" >
                    <div style="position: relative;">
                        <div 
                            class="main_write_frame animate__animated animate__fadeInLeft"
                            :class="mouseIn ? 'brightEffect' : ''"
                            @mouseenter="mouseIn = true"
                            @mouseleave="mouseIn = false"
                            @mousemove="mouseEffect"
                        >
                            <div>IT분야는 무궁무진한 미래 산업입니다.</div>
                            <div>이러한 최첨단 시대에 발맞추어 프론트엔트 개발자가 되는게</div>
                            <div>저의 목표이며, 또한 항상 새로움에 도전하고 싶습니다.</div>
                            <div>현재까지의 실무 경험과 개발 지식을 토대로 꾸준히 성장할 것이며</div>
                            <div>무엇보다 자신에게 당당한 개발자가 되겠습니다 :)</div>
                        </div>
                        <!-- <div class="circle"/> -->
                    </div>
                    
                    <v-avatar size="300" class="avatar_img__frame animate__animated animate__fadeInRight" rounded="true">
                        <img class="avatar_img img_front" src="@/assets/myFace.jpg" alt="alt">
                        <img class="avatar_img img_back" src="@/assets/aboutme.jpg" alt="alt">
                    </v-avatar>
                </div>
                <div class="career__frame" >
                    <div v-for="i in career" :key="i.name" class="career__frame--items animate__animated animate__fadeInUp animate__delay-1s">
                        <div class="career__frame--title">
                            {{i.name}}
                        </div>
                        <div class="divider"></div>
                        <div class="career__frame--list">
                            <div class="d-flex align-center flex-grow-1" v-for="i,index in i.list" :key="index">
                                <i 
                                    class="fas fa-circle fa-2xs" 
                                    style="color: #b7b7b7;"
                                />
                                <div 
                                    class="d-flex justify-space-between align-center flex-grow-1 "
                                    v-html="i"
                                />
                            </div>
                        </div>
                    </div>
                </div>
                <div  class="address__frame animate__animated animate__fadeInUp animate__delay-1s">
                    <cAbout 
                      v-for="i in inforList" 
                      :key="i.title"
                      :item="i"
                    />
                </div>  
            </div>
        </div>
    </v-layout>
</template>

<script>
export default {
    components: {
        cAbout: () => import('../components/cAbout.vue')
    },
    props: {},
    data() {
        return {
            endLanding: true,
            mouseIn:false,
            inforList:[
              {icon:'location-dot',title:'Location',contents:'대구광역시 남구'},
              {icon:'phone',title:'Phone',contents:'010-6208-4324'},
              {icon:'at',title:'Email',contents:'emfkrhwhem1@naver.com'},
            ],
            career: [
                { 
                    name: 'Career',
                    list: ['코즈모 <span>2020.06 ~ 2022.02 (1년 6개월)</span>'],
                },
                { 
                    name: 'Education',
                    list: ['수성대학교(IT융합과) <span>2018.02 ~ 2021.02</span>'],
                },
            ]
        };
    },
    watch: {
    },
    computed: {},
    created() {},
    mounted() {
        setTimeout(() => {
            this.endLanding = false
        }, 4000);
    },
    destroyed() {},
    methods: {
        mouseEffect(e){
            if (this.mouseIn) {
                let div = document.getElementsByClassName('main_write_frame');
                let rect = div[0].getBoundingClientRect()
                //let circle = document.getElementsByClassName('circle')
                div[0].style.setProperty('--cursorX', e.pageX - rect.left + 'px')
                div[0].style.setProperty('--cursorY', e.pageY - rect.top + 'px')
                /* this.gsap.to(circle, .3, {
                    css: {
                        left:   e.pageX - rect.left,
                        top: e.pageY - rect.top
                    }
                }); */
            }else{
                return
            }
        }
    },
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/app.scss";
.about_landing {
    display: flex;
    justify-content: center;
    align-items: center;
    background: #fff;
    width: 100%;
    height: 100%;
    &__font {
        color: rgb(27, 29, 32);
        font-size: 120px;
    }
}

.brightEffect{
    transition: 1s;
    --cursorX: 50vw;
    --cursorY: 50vh;
    &:before {
        content: '';
        display: block;
        width: 100%;
        height: 100%;
        position: absolute;
        pointer-events: none;
        background: radial-gradient(
            circle 10vmax at var(--cursorX) var(--cursorY),
            rgba(27,29,32,0) 0%,
            rgba(27,29,32,.5) 80%,
            rgba(27,29,32,.95) 100%
        )
    }
}
.main_write_frame {
    transition: 1s;
    position: relative;
    white-space: nowrap;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 30px;
    cursor: none;
    font-family: Chosunilbo_myungjo;
    & div:not(:last-child){
        padding-bottom: 10px;
    }
    
    
}

.avatar_img {
    position: absolute;
    object-fit: cover;
    backface-visibility: hidden;
    transition: 1s;
}
.avatar_img__frame {
    perspective: 500px;
    &:hover{
        background: #f5f5f5;
        .img_front{
            transform: rotateY(180deg);
        }
        .img_back{
            transform: rotateY(0deg);
        }
    }

    .img_front{
        position: absolute;
        transform: rotateY(0deg);
    }
    .img_back{
        transform: rotateY(-180deg);
    }
}

.contents__frame {
    align-items: center;
    justify-content: center;
    display: flex;
    gap: 40px;
    @supports not (gap: 40px) {
    .contents__frame {
        margin: -40px;
    }
    .contents__frame > * {
        margin: 40px;
    }
    }
}

.career__frame {
    display: grid;
    grid-template-columns: repeat(2, minmax(10%,500px));
    justify-content: center;
    padding-top: 40px;
    grid-gap: 160px;
    gap: 160px;
    &--title {
        padding-bottom: 5px;
        font-family: Chosunilbo_myungjo;
    }
    &--list {
        font-family: Chosunilbo_myungjo;
        font-size: 16px;
        font-weight: 500;
        padding-top: 10px;
        display: flex;
        line-height: initial;
        &::v-deep span{
            line-height: initial;
            margin-left: auto;
        }
        &::v-deep div{
            padding-left: 5px;
        }
    }
}

.divider {
    height: 2px;
    width: 100%;
    background: #f5f5f5;
}
.circle{ // 마우스를 따라다니는 원 설정
    position: absolute;
    width: 100px;
    height: 100px;
    top: 50%;
    left: 50%;
    cursor: none;
    border-radius: 50%;
    transform: translate(-50%, -50%);
    pointer-events: none;
    backdrop-filter: blur(3px);
    background: rgba(255, 255, 255, 0.05);
    box-shadow: 0 0 15px rgba(0,0,0,0.2);
}
.address__frame{
  display: grid;
  grid-template-columns: repeat(3, minmax(10%,500px));
  justify-items: center;
    justify-content: center;
  gap: 20px;
  padding-top: 40px;
}
</style>
